import React, { useState, useEffect, useRef} from "react";
import getWords  from "../palavras";
import './Words.css'
import Perfect from "../Animaters/Perfect";
import Score from "../Animaters/Score";
import Timer from "../Animaters/Timer";

function Words(props) {

    const [words,setWords] = useState()
    const [gameState, setGameState] = useState()
    const [scoreClasses, setScoreClasses] = useState('')
    const [wordAtual, setWordAtual] = useState()
    const [indiceWord, setIndiceWord] = useState(0)
    const [score, setScore] = useState(0)
    const [timerActive, setTimerActive] = useState(false)
    const [scoreGanho, setScoreGanho] = useState(0)
    const [sequencia, setSequencia] = useState(0)
    const [backspace, setBackspace] = useState(false)
    const [perfectState, setPerfectState] = useState(false)
    const inputRef = useRef()
  
    useEffect(() => {
        if (props.gameState === 'rodando') {
          setGameState('rodando')
          setTimerActive(true)
          const w = getWords()
          if (w) {
            setWords(w)
            setWordAtual(w[0])
          } 
            setTimeout(() => {      
              inputRef.current.focus()
          }, 100);
        }        
    }, [props])  
    
   function onHandleChange(){
        if (inputRef.current.value == wordAtual) { 
          inputRef.current.classList.remove('erro')
          if (backspace === true) {
            setScore(score + 1)
            setScoreGanho(score)
            setSequencia(0)
            setPerfectState(false)
          }
          if (backspace == false) {
            setScoreGanho(score)
            setPerfectState(true)
            
            setScore(score + 2 + sequencia)
            if (sequencia < 10) {
              setSequencia(sequencia + 1)
            }
          }
          
          const newindice = indiceWord + 1
          setIndiceWord(newindice)
          setWordAtual(words[newindice])
          setBackspace(false)
          inputRef.current.value=''
        }
     }

     document.addEventListener('keydown', (e) => {
      if (e.keyCode === 8 ) {
        if (backspace === false) {
          setBackspace(true)
        }
      }
     })

     document.addEventListener('keydown', (e) => {
      if (e.keyCode === 32 ) {
        if (inputRef.current.value != wordAtual) {
          inputRef.current.classList.add('erro')
      }
      }
     })

     useEffect(() => {
      if (perfectState == true) {
        setTimeout(() => {
          setPerfectState(false)
        }, 1000);
      }
     }, [perfectState])

     useEffect(() => {
      const diferença = score - scoreGanho
      setScoreGanho(diferença)
      setScoreClasses('ganharscore')
      setTimeout(() => {
          setScoreClasses('')
      }, 500);
   }, [score])

      return ( 
        <>
        
        <h1>{words ? wordAtual : <></>}</h1>
        {
          
          words ?
          <>
          <div className="box">
             <input onChange={onHandleChange} ref={inputRef} type='text' />
          </div>
          <div className="box" id="score">
            <h2 className={scoreClasses}>{score}</h2>
          </div>
          </>
         
          : <></>
        }
        <Score score={scoreGanho}  />
      <Perfect score={score} perfectState={perfectState}/>
      <Timer score={score} active={gameState}/>
      
     

        </>
        
       )

    
  }


export default Words