import React, {useEffect, useState, useRef} from 'react';
import './Perfect.css'

interface Score {
    score: number
}
function Score(props : Score){

    const [scoreAnterior, setScoreAnterior] = useState(0)
    const [score, setScore] = useState(0)
    const [scoreClasses, setScoreClasses] = useState('scoreGanho')

    const scoreRef = useRef(null)

    useEffect(() => {
        if (props.score !== score) {
            setScore(props.score)
            setTimeout(() => {
                setScoreClasses(`aparecer1 scoreGanho`)
              }, 200);
            
            setTimeout(() => {
                setScoreClasses('scoreGanho')
              }, 1000);
        }
    }, [props])



    return (
        <>
        {
            score > 0 ? <h3 ref={scoreRef} className={scoreClasses}> {`+${score}`}</h3> : <></>
        }

        </>
        
    )


}

export default Score