import React, {useEffect, useState} from 'react';
import './Perfect.css'

interface Perfect {
    perfectState: boolean
}
function Perfect(props : Perfect){
    const [perfect1, setPerfect1] = useState('perfect')
    const [perfect2, setPerfect2] = useState('perfect')
    const [perfect3, setPerfect3] = useState('perfect')

     useEffect(() => {
     const nmr = Math.floor(Math.random() * 2)
        if (props.perfectState === true) {
            if (perfect1 === 'perfect') {
                setPerfect1(`aparecer${nmr} perfect`)
                setTimeout(() => {
                    setPerfect1('perfect')
                  }, 1000);
            }
        }
     }, [props])  


    return (
        <>
        <h4 id='perfect' className={perfect1}>Perfect!</h4>
 
        </>
    )
}

export default Perfect