import React, { useEffect, useState, useRef } from 'react';
import './Perfect.css'
import Lottie from 'react-lottie';
import * as animationData from './LottieTimer.json'
import FinallyModal from '../Modal/FinallyModal';



function Timer(props) {

    const [timer, setTimer] = useState(45)
    const [score, setScore] = useState(0)
    const [active, setActive] = useState(false)
    const [final, setFinal] = useState(false)

    useEffect(() =>{

        

        if (props.score) {
            setScore(props.score)
        }

        if (props.active === 'rodando') {
            iniciartimer()
            setActive(true)
        }

        function iniciartimer() {
        if (!active) {
            let count = 45;

            const intervalId = setInterval(() => {
              count--
              setTimer(count)
            
              if (count == 0) {
                clearInterval(intervalId);
                setFinal(true)
                setActive(false)
                count = 45
              }
            }, 1000);
        }

        console.log(score);
       
    }
    }, [props])

    



    return (
        <> {
            active != false ? <div className='box'>
            <h2 id='timernumber'>{timer}</h2>
            <Lottie
            style={{marginTop: '100px'}}
                options={{
                animationData: animationData
                }}
                height={100}
                speed={2}
                loop={1}
            />
        </div> : <></>
        }
        {final ? <div id='finallyModal'>
           <FinallyModal score={score} onClose={() => {setFinal(false)}}/>
        </div> : <></>}
                
     


        </>
    )

}

export default Timer