import React, { useState, useRef, useEffect } from "react";
import App from '../App'

interface FinallyModal {
    score: number;
    onClose: Function
}

function FinallyModal(props: FinallyModal) {

    const [score, setScore] = useState<number>(0)
    const [inputValue, setInputValue] = useState<string>('')

    useEffect(() => {
        setScore(props.score)
    }, [props])
    

    function nomeChange(e : any){
        setInputValue(e.target.value)
    }

    function enviar(score: number, nome : string) {
        let values = []
        values.push(nome)
        values.push(score)
    
       fetch(`https://typegame.onrender.com/addRow`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(values),
      })
        .then((response) => response.json())
        .then((data) => {
          console.log("Success:", data);
          enviado()
        })
        .catch((error) => {
          console.error("Error:", error);
        });
      }

      function enviado(){
        window.location.reload()
      }

    return (
        <>
            <h1>Pontuação: <br /> <span>{props.score}</span> </h1>
            <input onChange={nomeChange} placeholder="Seu nome" className="inputNome" type="text" />
            <input onClick={() => {enviar(score, inputValue)}} className="inputNome" id="buttonenviar" value='enviar' type="button" />
        </>

       
    )
}

export default FinallyModal;