import React, {useState, useEffect, useRef} from 'react';
import './Iniciarbutton.css'
import './animations.css'

interface iniciarButton {
    onclick: () => void;
    gameState: string
}



function IniciarButton(props : iniciarButton){


const [startAnimte, setStartAnimte] = useState<'off' | 'on'>('off')

    const btnRef = useRef(null)

function desappear(){
    props.onclick()
    setStartAnimte('on')
}


return (
    <>
    {
        props.gameState == 'rodando' ?
        <></>
        :
        <button ref={btnRef} onClick={() => {desappear()}} className={props.gameState != 'off' ? 'button-82-pushable  desappear' : 'button-82-pushable'} role="button">
  <span className="button-82-shadow"></span>
  <span className="button-82-edge"></span>
  <span className="button-82-front text">
    Start
  </span>
</button>

    }
   
<div>
    
</div>
    </>
)

}

export default IniciarButton

