import React, {useEffect, useState} from 'react';



function Table(props){

  const [data, setData] = useState()

  useEffect(() => {
    setData(props.data)
  }, [props])

    return(
      <>
        <table className="table">
  <thead>
    <tr>
    <th scope="col">#</th>
      <th scope="col">Nome</th>
      <th scope="col">Score</th>
    </tr>
  </thead>
  <tbody>
  {data ? data.map((row, index) => (
          <tr key={index}>
            <td>{index+1 + '°'}</td>
            <td>{row[0]}</td>
            <td>{row[1]}</td>
          </tr>
        )): <></>}
  </tbody>
</table>
</>
    )

}

export default Table