// @ts-nocheck
import React, { useState, useEffect, useRef } from 'react';
import Lottie from 'react-lottie';
import * as animationData from './CounterLottie.json'
import './contador.css'




function Contador(props : any) {


    const defaultOptions = {
        loop: true,
        autoplay: true,
        animationData: animationData,
        rendererSettings: {
            preserveAspectRatio: 'xMidYMid slice'
        }
    };

    const styleoff = {
      display: 'none'
    }
    const styleon = {
        display: 'flex'
      }

    const counterRef = useRef(null)
    const [styleState, setStyleState] = useState(styleoff)
    const [gameState, setGameState] = useState('off')
    const [contador, setContador] = useState(
        { isStopped: true}
    )

    useEffect(() => {
        setGameState(props.gameState)
       if (props.gameState == 'on') {
        setTimeout(() => {
            iniciarContador()
        }, 1000);
        if (props.gameState == 'rodando') {
            setContador({isStopped: true})
        }

    
       }
    }, [props])

    function iniciarContador() {
        setStyleState(styleon)
        setContador({isStopped: false})
        setTimeout(() => {
            setStyleState(styleoff)
        }, 3000);
    }

    const btnRef = useRef(null)


    return (
        <div>
            {
                props.gameState == 'on'
                ? 
                <div className='contador'>
                <Lottie ref={counterRef} options={defaultOptions}
                    height={300}
                    width={300}
                    style={styleState}
                    isStopped={contador.isStopped} />
            </div>
            : 
            <></>

            }
        
       
        </div>
    )
}

export default Contador

