import React, {useState} from 'react';
import Contador from './Contador/Contador';
import IniciarButton from './Menu/IniciarButton';
import Words from './Words/Words';
import Leaderboard from './Leaderboard/Leaderboard';


function App() {

  const [gameState, setGameState] = useState('off')
  const [leaderboard, setLeaderboard] = useState(false)

  function closeLeaderboard(){
    setLeaderboard(false)
  }
  
  console.log(gameState);

  function rodarGame(){
      setGameState('rodando')
      console.log('rodando');
  }

  function StartClick() {
    if (gameState == 'off') {
        setGameState('on')
        setTimeout(() => {
          rodarGame();
        }, 4000);
    }
} 

  return (
    <> 
    {gameState == 'off' ? <div onClick={() => {setLeaderboard(true)}} id='leaderboardbtn'><i className="bi bi-trophy-fill"></i></div>  : <></>}
    {gameState == 'off' || gameState == 'on' ?  <IniciarButton gameState={gameState} onclick={StartClick}/> : <></>}
   
      <Contador gameState={gameState}/>
      <Words gameState={gameState}/>

      {leaderboard == true ? <Leaderboard state={leaderboard} onClose={closeLeaderboard}/> : <></> }
      
      
      </>
      
      
  );
  
}

export default App;
