import React, {useState, useEffect} from 'react';
import './Leaderboard.css'
import Table from './Table';

function Leaderboard(props){

    const [data, setData] = useState()

    useEffect(() => {
        async function fetchData() {
          const response = await fetch('https://typegame.onrender.com/getRows');
          const res = await response.json();
         console.log(res);
         setData(((res.values).sort((a, b) => b[1] - a[1])).slice(0, 10))
        }
        fetchData();
      }, []);

    return (
        <>
       
       <div id='leaderboard'>
       <div onClick={props.onClose} id='btnclose'><i class="bi bi-x-circle hover"></i></div>
       <Table data={data}/>
       </div>
       
        </>
    )


}

export default Leaderboard